	margin-left: 350px;
	margin-top: -80px;
}

.title-no-schedules {
	color: #ffffff;
	text-align: center;
	padding-top: 100%;
}

.container {
	background: #232931;
	width: 540px;
	height: 500px;
	margin: 0 auto;
	position: relative;
	margin-top: 5%;
	border-radius: 8px;
	box-shadow: 2px 5px 20px rgba(119, 119, 119, 0.5);
}

.leftbox {
	top: -5%;
	left: 5%;
	position: absolute;
	width: 15%;
	height: 110%;
	background-color: #50d890;
	box-shadow: 3px 3px 15px rgba(119, 119, 119, 0.5);
}

nav {
	margin: 2.6em auto;
}

nav a {
	list-style: none;
	padding: 35px;
	color: #232931;
	font-size: 1.1em;
	display: block;
	transition: all 0.5s ease-in-out;
	cursor: context-menu;
}

.rightbox {
	margin-left: 7em;
	height: 100%;
	width: 200px;
	top: -0.5%;
	position: absolute;
	overflow: auto;
}

.rb-container {
	width: 100%;
	display: block;
	position: relative;
}

::-webkit-scrollbar { 
	display: none; 
}

.rb-container ul.rb {
	margin: 2.5em 0;
	display: inline-block;
	width: 158px;
}

.rb-container ul.rb li {
	list-style: none;
	margin: auto;
	min-height: 50px;
	border-left: 1px dashed #fff;
	padding: 0 0 50px 30px;
	position: relative;
}

.rb-container ul.rb li:last-child {
	border-left: 0;
}

.rb-container ul.rb li::before {
	position: absolute;
	left: -18px;
	top: -5px;
	content: " ";
	border: 8px solid rgba(255, 255, 255, 1);
	border-radius: 500%;
	background: #50d890;
	height: 20px;
	width: 20px;
	transition: all 500ms ease-in-out;
}

.rb-container ul.rb li:hover::before {
	border-color: #232931;
	transition: all 1000ms ease-in-out;
}

ul.rb li .timestamp {
	color: #50d890;
	position: relative;
	width: 100px;
	font-size: 11px;
}

.item-title {
	color: #fff;
	font-size: 16.5px;
}

.title {
	color: #50d890;
	font-size: 25px;
    margin-left: 3.5em;
    margin-top: -2em;
}

